import gql from 'graphql-tag';

export const query = gql`
  query ($page: String!){
    cards(page : $page){
      id,
      title,
      image,
      body
    }
  }
`;

export const options = {
  options: {
    variables: {
      page: "/expertise/data", // eslint-disable-line
    },
  },
};



